.page-container {
  width: 100%;

  &__header {
    width: 100%;

    display: flex;
    flex-flow: column nowrap;
    justify-content: center;
    align-items: center;

    &__side-container {
      display: flex;
      flex-flow: column nowrap;
      justify-content: space-around;

      &__social-links {
        display: flex;
        flex-flow: row nowrap;
        justify-content: space-around;
      }

    }

  }

  &__nav {
    /* max-width is 100% */

    &__list {
      width: 100%;

      display: flex;
      flex-flow: column nowrap;
      justify-content: center;

      &__item, &__item--outside__left, &__item--outside__right {
        display: flex;
        align-items: center;

        &__link {
          width: 100%;
        }

      }

      &__item--dropdown {
        display: flex;
        flex-direction: column;
        justify-content: flex-end;

        &__list {
          margin: 0;
          padding: 0;

          &__item {

            &__link {
              width: 100%;
            }

          }

        }

      }

    }

  }

  &__mobile-nav {
    width: 100%;

    &__list {
      width: 100%;

      display: flex;
      flex-flow: row nowrap;
      justify-content: flex-start;

      &__item {
        display:flex;
        flex-flow: row nowrap;
        justify-content: space-around;
      }

    }

  }

  &__home-body {
    width: 100%;

    &__form-list {
      width: 100%;

      display: flex;
      flex-flow: column nowrap;
      justify-content: center;
      align-items: center;
    }

  }

  &__footer {
    width: 100%;

    &__container {
      width: 100%;
      box-sizing: border-box;

      display: flex;
      flex-flow: column wrap;
      justify-content: center;
      align-items: center;

      &__contact {
        display: flex;
        flex-flow: column wrap;
        justify-content: center;
        text-align: center;

        &__top, &__bottom {
          display: flex;
          flex-flow: row wrap;
          justify-content: center;
        }

      }

    }

  }

  @media screen and (width >= $bp-nostack) {

    &__header {
      width: 100%;

      display: flex;
      flex-flow: row nowrap;
      justify-content: flex-end;
      align-items: initial;

      &__side-container {
        padding: 1.5em 0;

        &__social-links {
          justify-content: space-between;
        }

      }

    }

    &__nav {
      display: flex;
      flex-flow: row nowrap;
      justify-content: center;

      &__list {
        display: flex;
        flex-flow: row nowrap;
        justify-content: center;

        width: 80%;

        &__item, &__item--outside__left, &__item--outside__right {
          display: flex;
          align-items: center;

          &__link {
            display: block;
            width: auto;
          }

        }

        &__item--dropdown {
          display: flex;
          flex-direction: column;
          justify-content: flex-end;;

          &__list {
            margin: 0;
            padding: 0;
          }

        }

      }

    }

    &__mobile-nav {
      display: none;
    }

    &__home-body {
      width: 85%;

      &__form-list {
        width: 100%;

        display: flex;
        flex-flow: row wrap;
        justify-content: center;
      }

    }

    &__footer {
      width: 100%;

      &__container {
        width: 100%;

        display: flex;
        flex-flow: row nowrap;
        justify-content: center;
        align-items: none;

        &__contact {
          display: flex;
          flex-flow: column wrap;
          justify-content: center;

          &__top, &__bottom {
            display: flex;
            flex-flow: row nowrap;
            justify-content: space-around;
          }

        }

      }

    }

  }

  @media screen and (width >= $bp-large) {

    &__header {
      width: 80%;
    }

    &__footer {

      &__container {
        margin: 0 auto;
        width: 80%;
      }

    }

  }

  @media screen and (width >= $bp-xlarge) {

    &__header {
      width: 60%;
    }

    &__home-body {
      width: 70%;
    }

    &__footer {

      &__container {
        margin: 0 auto;
        width: 60%;
      }

    }

  }

}
