@import '../../states/main/nav/dropdown.css';
@import '../../states/main/nav/outside.css';

.page-container__nav {
  max-width: 100%;

  overflow-x: hidden;

  transition: all 1s;

  background-color: $fair-blue;

  box-sizing: border-box;

  &__list {
    padding: 0;
    margin: 0;

    list-style-type: none;

    &__item {
      border-bottom: 1px solid #bdbdbd;

      &:hover {
        background-color: $fair-red;
      }

      &__link {
        padding: .5em 1em;

        color: white;
        font-size: 1.5em;
        text-decoration: none;

      }

    }

  }

  &.closed {
    max-width: 0;
    max-height: 0;
  }

  &.closed &__list__item, &.closed &__list__item--dropdown, &.closed &__list__item--outside__left {
    border-bottom: none;
    max-width: 0;
  }

  @media screen and (width >= $bp-nostack) {
    display: block;
    background-color: $fair-blue;
    border-bottom: .25em solid $fair-red;

    &__list {
      margin: auto;
      padding: 0;

      list-style-type: none;

      &__item {
        border-left: 1px solid #bdbdbd;
        border-right: 1px solid #bdbdbd;
        border-bottom: none;

        &:hover {
          background-color: $fair-red;
        }

        &__link {
          padding: .5em 2em;

          color: white;
          font-size: 1.25em;
          text-decoration: none;

        }

      }

    }

    &.closed {
      max-width: none;
      max-height: none;
    }

    &.closed &__list__item, &.closed &__list__item--dropdown, &.closed &__list__item--outside__left {
      border-bottom: none;
      max-width: none;
      max-height: none;
    }

  }

}
