.form-container__form__fieldset__section__field__input--date {
  width: 100%;

  margin: 0.25em 0;
  padding: 0.25em;

  border: 1px solid #cccccc;
  box-shadow: inset 0 1px 2px rgba(0, 0, 0, 0.1);

  font-size: 2em;
  font-family: monospace;

  @media screen and (width >= $bp-nostack) {
    width: auto;

    margin-right: 1.25em;

    font-size: 1.25em;
    font-family: inherit;
  }

}
