.page-container__home-body {
  border-top: .25em solid $fair-red;

  padding: .5em 0;

  &__download-button {
    width: 90%;
    padding: 1em;

    border: .2em solid lightgrey;

    border-radius: 5px;

    background-color: $fair-blue;
  }

  @media screen and (width >= $bp-nostack) {
    margin: 0 auto;

    border-top: none;
    padding-top: 1em;
  }

}
