.page-container__mobile-nav {
  background-color: $fair-red;
  box-sizing: border-box;

  &__list {
    padding: 0;
    margin: 0;

    list-style-type: none;

    &__item {

      &__text {
        padding: .5em .25em;

        color: white;
        font-size: 1.5em;
        text-decoration: none;

      }

      &__hamburger {
        padding: .5em .25em;
        padding-left: 1em;

        color: white;
        text-decoration: none;

      }

    }

    &:hover {
      cursor: pointer;
      background-color: $fair-red;
    }

  }

  &.closed {
    background-color: $fair-blue;
  }

}
