.form-container__form__fieldset__radiogroup {

  &__container {

    &__field--last, &__field--clicked {
      box-sizing: border-box;
      padding: 1em;

      border: 1px solid $fair-blue;
      background-color: $fair-blue;
      color: white;

      &__label {
        margin: 0.25em 0;

        font-size: 1em;

        &:hover {
          cursor: pointer;
        }

      }

      &__input {
        display: none;
      }

      &:hover {
        cursor: pointer;

        background-color: white;
        color: $fair-blue;
      }

      &:active {
        border: 1px solid yellow;
      }

    }

    &__field--last {
      width: 100%;
    }

    &__field--clicked {
      background-color: white;
      color: $fair-blue;
      border: 1px solid $fair-blue;
    }

  }

  @media screen and (width >= $bp-nostack) {

    &__container {

      &__field--clicked {
        width: auto;
        background-color: none;
        color: black;
        border: none;
      }

      &__field--last {
        width: auto;
        box-sizing: content-box;
        margin-right: 1em;
        padding: 0;

        border: none;
        background-color: white;
        color: black;

        &__input {
          display: block;

          margin-right: 0.2em;
        }

        &:hover {
          cursor: auto;

          color: black;
        }

        &__label {
          font-size: 1.25em;

          &:hover {
            cursor: auto;
          }

        }

      }

    }

  }

}
