/* FORM FOOTER */

/* CONFIG */

/* BASE */
.form-container__footer {
  padding: .5em 1em;

  border: .5em solid $fair-blue;
  border-bottom-left-radius: 3em;
  border-bottom-right-radius: 3em;

  background-color: $fair-blue;

  & label {
    font-size: 2em;

    color: white;
  }

  @media screen and (width >= $bp-nostack) {
    padding: .5em 2em;

    & label {
      font-size: 1.5em;
    }

  }

}
