.page-container__footer {
  border-top: .5em solid $fair-blue;

  &__container {
    padding: .5em 0;

    &__img {
      padding-bottom: .25em;
    }

    &__contact {
      padding-top: .25em;

      &__top {

        &__text {
          padding: .25em 1em;

          font-size: 1.1em;
        }

      }

      &__divider {
        width: 90%;
        box-sizing: border-box;
      }

      &__bottom {
        list-style-type: none;
        padding: 0;
        margin: 0;

        &__text {
          font-size: 1.1em;

          padding: 0 .25em;
        }

      }

    }

  }

  @media screen and (width >= $bp-nostack) {

    &__container {
      padding: 1em;

      &__img {
        padding-right: 2em;
        padding-bottom: 0;
      }

      &__contact {
        padding-top: 0;

        &__top {

          &__text {
            padding: 0 1em;
          }

        }

        &__bottom {

          &__text {
            padding: 0;
          }

        }

        &__divider {
          width: 100%;
        }

      }

    }

  }

  @media screen and (width >= $bp-large) {

    &__img {
      margin-right: auto;
    }

  }

}
