/* ****** HEADER ****** */


/* Config */
$img-background-color: white;

$title-color: white;

/* Base */
.form-container__head {
  border: .5em solid $fair-blue;

  border-top-left-radius: 3em;
  border-top-right-radius: 3em;

  &__img-block {

    & #logo {
      display: block;
      max-width: 80%;

      margin: 0 auto;
      padding: 1em;
    }

  }

  &__title-block {
    margin-bottom: -1px;

    border-top: .5em solid $fair-blue;

    background-color: $fair-blue;

    text-align: center;

    &__title {
      display: block;
      max-width: 80%;

      margin: 0 auto;
      padding: .25em;

      font-size: 2em;
      font-weight: bold;

      color: white;
    }

  }

  /* MEDIA QUERIES */
  @media screen and (width >= $bp-nostack) {

    &__title-block {

      &__title {
        font-size: 2em;
      }

    }

  }

  @media screen and (width >= 550px) and (width < $bp-medium) {

    &__img-block {

      & #logo {
        max-width: 60%;
      }

    }

  }

  @media screen and (width >= $bp-medium) {

    &__title-block {
      border-top: 0;

      border-top-right-radius: 2em;
    }

  }

  @media screen and (width >= $bp-large) {

    &__title-block {

      &__title {
        font-size: 2.5em;
      }

    }

  }

}
