.form-container {
  width: 100%;

  &__head {
    width: 100%;

    box-sizing: border-box;

    &__img-block {
      width: 100%;
    }

    &__title-block {
      width: 100%;

      box-sizing: border-box;
    }

  }

  &__form {
    width: 100%;

    box-sizing: border-box;

    &__fieldset {

      &__section {
        display: flex;
        flex-flow: row wrap;
        justify-content: center;

        /* CLEAR FIX */
        &:after {
          content: "";
          display: block;
          clear: both;
        }

        &__field {
          width: 100%;

          &__input {
            width: 100%;
          }

        }

      }

      &__radiogroup {

        &__container {
          width: 100%;

          display: flex;
          flex-flow: row wrap;
          justify-content: center;

          box-sizing: border-box;

          &__field, &__field--last, &__field--clicked {
            width: 50%;

            display: flex;
            align-items: center;

            text-align: center;

            &__label {
              width: 100%;
            }

          }

        }

      }

      &__pagination-controls {
        display: flex;
        flex-flow: row nowrap;
        justify-content: center;



        &__button {
          margin: 0 1em;
        }

      }

    }

  }

  &__footer {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    box-sizing: border-box;

    width: 100%;
  }

  @media screen and (width >= $bp-nostack) {

    &__form {

      &__fieldset {

        &__section {
          justify-content: flex-start;

          &__field {
            width: auto;

            &__input {
              width: auto;
            }

          }

        }

        &__radiogroup {
          display: flex;
          flex-flow: row wrap;
          justify-content: flex-start;

          &__container {
            justify-content: flex-start;

            &__field {
              width: auto;

              &__label {
                width: auto;
              }

            }

          }

        }

      }

    }

    &__footer {
      flex-flow: row nowrap;
      justify-content: space-between;
    }

  }

  @media screen and (width >= $bp-medium) {
    max-width: 80%;
    margin: 1em auto;

    &__head {
      display: flex;

      &__img-block {
        flex: 1;

        display: flex;
        align-items: center;
      }

      &__title-block {
        flex: 1;

        display: flex;
        align-items: center;
      }

      /* CLEAR FIX */
      &:after {
        content: "";
        display: block;
        clear: both;
      }

    }

  }

  @media screen and (width >= $bp-large) {
    max-width: 60%;
  }

}
