.page-container__home-body__form-list__form-card {
  height: 23.5em;
  width: 90%;

  padding: .5em;

  -webkit-perspective: 800;
  position: relative;

  &__card {
    width: 100%;
    height: 100%;
    transform-style: preserve-3d;
    transition: 0.5s;

    &.flipped {
      transform: rotatey(-180deg);
    }

    &__front, &__back {
      width: 100%;
      height: 100%;
      position: absolute;
      -webkit-backface-visibility: hidden;
      z-index: 2;
      font-size: 1em;
      box-sizing: border-box;

      border: .25em solid	#bdbdbd;
      border-radius: 5px;
    }

    &__front {
      position: absolute;
      z-index: 1;
      color: white;
      cursor: pointer;
      display: flex;
      flex-flow: column wrap;
      justify-content: center;

      background-color: $fair-blue;

      &__title {
        background-color: #bdbdbd;
        text-align: center;

        &__text {
          margin: 0;
          padding: 1em 0;
          font-size: 1.5em;

          color: $fair-blue;

          transition: color .5s;

        }

      }

      &__text {
        margin: auto 0;
        font-size: 1.5em;
        padding: 0 1em;
      }

      &:hover &__title__text {
        color: white;
      }

    }

    &__back {
      -webkit-transform: rotatey(-180deg);
      color: $fair-blue;

      display: flex;
      flex-flow: column nowrap;
      justify-content: flex-start;

      &__return {
        display: flex;
        flex-flow: row nowrap;
        justify-content: flex-end;
        align-items: center;

        padding: .5em;

        &__link {
          transition: color .5s;

          &:hover {
            color: $fair-red;
            cursor: pointer;
          }

        }

      }

      &__links {
        display: flex;
        flex-flow: column wrap;
        justify-content: flex-start;

        padding: 0 .5em;

        &__link {
          color: white;
          text-decoration: none;
          padding: .4em 0;

          &__button {
            width: 100%;
            padding: 1rem 2rem 1.0369rem 2rem;

            border: 1px solid $fair-blue;
            border-radius: 3px;

            text-align: center;
            cursor: pointer;

            font-size: 1.25em;
            color: white;
            background-color: $fair-blue;

            transition: color .5s, background-color .5s;

            & a {
              color: inherit;
              text-decoration: none;
            }

            &:hover {
              color: $fair-blue;
              background-color: white;
            }

          }

        }

      }

    }

  }

  @media screen and (width >= $bp-nostack) {
    height: 21em;
    width: 13.5em;

    border-bottom: none;
    padding: 2em 1em;

    &__card__front {

      &__text {
        font-size: 1.25em;
      }

      &__title {

        &__text {
          padding: .5em 0;

        }

      }

    }

    &__card__back {

      &__links__link__button {
        font-size: 1em;
      }

      &__return {
        padding: .5em;
      }

    }

  }

}
