.page-container__nav__list__item--dropdown {

  border-bottom: 1px solid #bdbdbd;

  &__link {
    padding: .5em 1em;

    color: white;
    font-size: 1.5em;
    text-decoration: none;
  }

  &__list {
    display: block;
    text-decoration: none;
    list-style-type: none;

    overflow-y: hidden;
    max-height: 104px;

    transition: all 1s;

    &__item {
      border-top: 1px solid #bdbdbd;

      display: flex;
      flex-flow: row nowrap;
      justify-content: center;

      &__link {
        padding: .5em 1em;

        color: white;
        font-size: 1.5em;

        text-decoration: none;
      }

      &:hover &__link {
        background-color: $fair-red;
      }

    }

    &.closed {
      max-height: 0;
    }

  }

  &:hover {
    background-color: $fair-red;
  }

  &:hover &__list__item {
    background-color: $fair-blue;
  }

  @media screen and (width >= $bp-nostack) {
    border-left: 1px solid #bdbdbd;
    border-right: 1px solid #bdbdbd;
    border-bottom: none;

    &__link {
      font-size: 1.25em;
      padding: .5em 2em;
    }

    &:hover &__list {
      display: block;
    }

    &__list {
      display: none;
      position: absolute;

      z-index: 3;

      border-top: .25em solid $fair-red;
      border-bottom-left-radius: .5em;
      border-bottom-right-radius: .5em;

      oveflow-y: initial;
      max-height: none;

      transition: none;

      &__item {

        &__link {
          font-size: 1.25em;
          padding: .5em 2em;
        }

      }

      &.closed {
        max-height: none;
      }

    }

    &__link {
      background-color: transparent;
    }

  }

}
