@import '../../states/form/radiogroup.css';
@import '../../states/form/date-input.css';
@import '../../states/form/link-single.css';
@import '../../states/form/submit-button.css';

/* FORM BODY */

/* Config */

.form-container__form {
  margin-top: 1em;
  margin-bottom: 0;

  padding-top: 1em;

  border: .5em solid $fair-blue;

  &__fieldset {
    border: none;

    &__heading {
      text-decoration: underline;
    }

    &__section {

      &__field {
        margin: 0.75em 0;

        &__label {
          font-size: 2em;
        }

        &__input {
          margin: 0.25em 0;
          padding: 0.25em;

          border: 1px solid #cccccc;
          box-shadow: inset 0 1px 2px rgba(0, 0, 0, 0.1);

          font-size: 2em;

          &.ng-invalid.ng-touched {
            border: 1px solid red;
          }

        }

      }

      &__button {
        margin: 0 .5em;
        padding: 1rem 2rem 1.0369rem 2rem;

        border-width: 0px;
        border-radius: 3px;

        text-align: center;
        cursor: pointer;

        font-size: 1em;
        color: white;
        background-color: $fair-blue;
      }

    }

    &__radiogroup {
      margin: 0.75em 0;

      &__heading {
        margin-right: auto;

        font-size: 2em;
      }

      &__container {
        margin: 0.25em 0;

        &__field {
          box-sizing: border-box;
          padding: 1em;

          border: 1px solid $fair-blue;
          background-color: $fair-blue;
          color: white;

          &__label {
            margin: 0.25em 0;

            font-size: 1em;

            &:hover {
              cursor: pointer;
            }

          }

          &__input {
            display: none;
          }

          &:hover {
            cursor: pointer;

            background-color: white;
            color: $fair-blue;
          }

          &:active {
            border: 1px solid yellow;
          }

        }

      }

    }

    &__pagination-controls {

      &__link {

        &__button {
          margin: 0 .5em;
          padding: .75rem 1.5rem 0.78rem 1.5rem;

          border-width: 0px;
          border-radius: 3px;

          text-align: center;
          cursor: pointer;

          font-size: 1.5em;
          color: white;
          background-color: $fair-blue;
        }

      }

    }

  }

  @media screen and (width >= $bp-nostack) {

    &__fieldset {

      &__section {

        &__field {
          margin: 0;

          &__input {
            margin-right: 1.25em;
            font-size: 1.25em;
          }

          &__label {
            margin-right: .5em;
            font-size: 1.25em;
          }

        }

        &__button {
          margin: 1em 0;
        }

      }

      &__radiogroup {

        &__heading {
          font-size: 1.25em;
          text-decoration: underline;
        }

        &__container {

          &__field {
            width: auto;
            box-sizing: content-box;
            margin-right: 1em;
            padding: 0;

            border: none;
            background-color: white;
            color: black;

            &__input {
              display: block;

              margin-right: 0.2em;
            }

            &:hover {
              cursor: auto;

              color: black;
            }

            &__label {
              font-size: 1.25em;

              &:hover {
                cursor: auto;
              }

            }

          }

        }

      }

      &__pagination-controls {

        &__link {

          &__button {
            font-size: 1.25em;
          }

        }

      }

    }

  }

}
