.page-container__header {
  padding-top: 1em;

  box-sizing: border-box;

  &__img {
    max-width: 90%;
  }

  &__side-container {
    padding-top: 1em;

    &__phone {
      font-size: 1.5em;
      text-align: center;

      &__text {
        color: $fair-red;
      }

    }

    &__social-links {
      padding: 1em 1.5em;
      padding-bottom: 1em;

      &__link {
        color: #bdbdbd;

        &:hover {
          color: $fair-red;
        }

      }

    }

  }

  @media screen and (width >= $bp-nostack) {
    margin: 0 auto;
    padding: 1em;

    &__img {
      margin-right: auto;
      max-width: none;
    }

    &__side-container {

      &__phone {
        font-size: 1.25em;

        &__text {
          color: $fair-red;
        }

      }

      &__social-links {
        padding: 0 3em;
      }

    }

  }

  @media screen and (width >= $bp-large) {

  }

}
