.page-container__nav__list__item--outside {

  &__left, &__right {

    &:hover {
      background-color: $fair-red;
    }

    &__link {
      padding: .5em 1em;

      color: white;
      font-size: 1.5em;
      text-decoration: none;

    }

  }

  &__left {
    border-bottom: 1px solid #bdbdbd;
  }

  @media screen and (width >= $bp-nostack) {

    &__left, &__right {

      &:hover {
        background-color: $fair-red;
      }

      &__link {
        padding: .5em 2em;

        color: white;
        font-size: 1.25em;
        text-decoration: none;

      }

    }

    &__left {
      border-right: 1px solid #bdbdbd;
      border-bottom: none;
    }

    &__right {
      border-left: 1px solid #bdbdbd;
    }

  }

}
